<template>
  <v-layout align-center justify-center>
    <v-flex xs36 sm18 md6 lg15 xl19>
      <v-card>
        <v-form action="Proyecto-Ing-WEB\Frontend-Vue\public\backend-php\api\Login.php" method="post" ref="form" v-model="valid" lazy-validation>
          <v-toolbar dark color="red lighten-1">
            <v-toolbar-title>
              Acceso al Sistema
            </v-toolbar-title>
          </v-toolbar>
          <v-card-text class="pb-0">
            <v-spacer class="ma-5"></v-spacer>
            <v-text-field v-model="email" :rules="emailRules" autofocus color="green" label="Correo Electrnico" outlined required></v-text-field>
            <v-spacer class="ma-1"></v-spacer>
            <v-text-field v-model="password" :rules="passRules" type="password" color="green" label="Contrasea" outlined required></v-text-field>
            <v-flex class="red--text" v-if="error">
              {{error}}
            </v-flex>
          </v-card-text>
          <v-card-actions class="pt-0 px-3 pb-3">
            <v-flex text-xs-right>
              <v-btn @click="validate, ingresar" dark color="red lighten-1">Ingresar</v-btn>
            </v-flex>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import axios from 'axios'
export default {
  data: () => ({
    valid: false,
    password_hash: '',
    passRules: [
      v => !!v || 'Coloque su contrasea',
    ],
    email: '',
    emailRules: [
      v => !!v || 'Coloque su correo',
      v => /.+@.+\..+/.test(v) || 'Correo invlido',
    ],
  }),

  methods :{
    validate () {
      this.$refs.form.validate()
    },
    ingresar(){
      this.error=null;
      axios.post('api/Login.php', {email: this.email, password_hash: this.password_hash})
      .then(respuesta => {
        return respuesta.data
      })
      .then(data => {
        this.$store.dispatch("guardarToken", data.token)
        this.$router.push({ name: 'inicio' })
      })
      .catch(err => {
        //console.log(err.response);
        if (err.response.status==400){
            this.error="No es un email vlido";
        } else if (err.response.status==404){
            this.error="No existe el usuario o sus datos son incorrectos";
        }else{
            this.error="Ocurri un error";
        }
        //console.log(err)
      })
    }
  }
}
</script>

